.topImg{
    height: 450px;
    width: 80%;
    margin: 100px auto;
    background-image: url("../../asserts/services.jpg" );
    border-radius: 40px;
    display:flex;
    justify-content: center;
    flex-direction: column;
}
.btn {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.333;
    padding: 13px 27px;
    display: inline-block;
    box-sizing: border-box;
    border: 3px solid #fff;
    background-color: #ededeb;
    box-shadow: 0 4px 20px #000a24;
    color: black;
    transition: background .1s linear;
    margin-top: 60px;
    border-radius: 10px;
    width: 20%;
    text-align: center;
    margin-left: 40%;
}

.info{
    width: 70%;
    margin: 100px auto;
    height: auto;
    background-color: white;
    border-radius: 25px;
    height: 1000px;
}
.notNowWotToDo {
    height: 200px;
    background-color: #03b2cb;
    border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;
}

.notNowWotToDo h1 {
    text-align: center;
    margin-top: 30px;
}
